import React, {Fragment} from 'react';
import classes from "./Optional.module.css";
import {Helmet} from "react-helmet";
import { HashLink as Link } from 'react-router-hash-link';

import Layout from '../../../../../Components/Layout/Layout';
import SwiftCodeBlock from '../../../../../Components/SwiftCodeBlock/SwiftCodeBlock';
import SeeAlso from '../../../../../Components/SeeAlso/SeeAlso';

function App() {
  return (
    <Fragment>
        <Helmet>
          <title>Optional - iOS Swift | TheCodeClass</title>
        </Helmet>

    <Layout mainTitle = "Swift References"
            longText = "What is Optional in Swift?"
            keyText = "Optional- iOS Swift"
            youtubesrc = ""
            downloadHref = ""
            referenceHref = "https://docs.swift.org/swift-book/LanguageGuide/OptionalChaining.html"
            styling = {classes.slidersContent}> 

      <p>Variable or constants that you see it before contains a value such as Int number = 5. However,
      sometime, it can be empty or nil. For example, when asking a user about their age and it not required 
      for us if they do not want to enter this information. Hence, when he/she will submit nil as a value. </p>
      
      <h3>How can we do it?</h3>
      <p><strong>optional </strong> safety feature created by swift which can represent as a value 
      or absense of value. Therefore, optional can hold a <b>value</b> or <b>nil</b>. For example,
      opional variable or constant is an box that can contain one value such as "Wes", 3 or 3.2 or empty. 
      it is empty, it said to contain nil. An optional is referring to the box and not the item inside it.</p>

      <h4>Create a optional</h4>
      <p>You can create a optional by declaring a question mark after the data type. This means 
      you create an box which can contain value or nil unlike the normal variable which contain 
      only value.</p>

      <SwiftCodeBlock>
        <span className = "purple">var </span><span className ="blue">age:</span> <span className = "purpleLight">Int</span>?
      </SwiftCodeBlock>

      <p>After that you can assign it to a value or nil, if you did assign to anything, it is still 
      empty (nil)</p>
      <SwiftCodeBlock>
        <span className ="blue">age</span> = <span className = "darkBlue">26</span> <br/>
        <span className ="blue">age</span> = <span className = "purple">nil</span>
      </SwiftCodeBlock>
      <br/>
      <h3>Optionals</h3>
      <p>Now you want to looks what is inside the box. How will it looks like when you retrieve 
      the information.</p>
      <SwiftCodeBlock>
        <span className = "purple">var </span><span className ="blue">age:</span><span className = "purpleLight">Int</span>? = <span className = "darkBlue">26</span> <br/>
        <span className ="purple">print</span>( <span className = "blue">age</span> )
      </SwiftCodeBlock>
      <br/>
      <SwiftCodeBlock output = {true}>
        Optional(26)<br/>
      </SwiftCodeBlock>

      <p>The output is not what you wanted because the result is an optional that contains the value 26.
      If you take an opional as an normal integer to do a arithmetic operation. It will resulted in 
      an error because you are trying to mutiple the optional or box and not the value inside.</p>
      <SwiftCodeBlock>
        <span className = "purple">var </span><span className ="blue">age:</span><span className = "purpleLight">Int</span>? = <span className = "darkBlue">26</span> <br/>
        <span className ="purple">print</span>( <span className = "blue">age</span> * <span className = "darkblue">3</span>)
      </SwiftCodeBlock>
      <br id = "unwrapping"/>
      <SwiftCodeBlock output = {true}>
      error: value of optional type 'Int?' must be unwrapped to a value of type 'Int'<br/>
      </SwiftCodeBlock>
      <h3>Unwrapping optionals</h3>
      <p>The error message tells you that the optional have to be unwrapped. There are three ways 
      to do that</p>
      <ol>
        <li>Force Unwrapping</li>
        <li>Optional binding</li>
        <li>Nil coalescing</li>
        <li>Unwrapping with guard</li>
      </ol>

      <h3>1. Force Unwrappings</h3>
      <p>You can force unwrapping it meaning you tell the Swift that is is an value inside and it 
      is not nil. To wrap it, use exclamation mark after the variable name which tells the compiler 
      that you want to look inside the box and take out the value.</p>
      <SwiftCodeBlock>
        <span className = "purple">var </span><span className ="blue">age:</span><span className = "purpleLight">Int</span>? = <span className = "darkBlue">26</span> <br/>
        <span className ="purple">print</span>( <span className = "blue">age!</span> * <span className = "darkblue">3</span>)
      </SwiftCodeBlock>
      <br/>
      <SwiftCodeBlock output = {true}>
      78<br/>
      </SwiftCodeBlock>

      <p>However, you will get an error if you force unwrap an nil value.</p>
      <SwiftCodeBlock>
        <span className = "purple">var </span><span className ="blue">age:</span><span className = "purpleLight">Int</span>? = <span className = "purple">nil</span> <br/>
        <span className ="purple">print</span>( <span className = "blue">age</span> * <span className = "darkblue">3</span>)
      </SwiftCodeBlock>
      <br/>
      <SwiftCodeBlock output = {true}>
      Fatal error: Unexpectedly found nil while unwrapping an Optional value: file<br/>
      </SwiftCodeBlock>

      <p><strong>How can you play it safe?</strong></p>
      <p>A way to play it safe is to use an if statement and excute the code if it is not nil. However, this
      solved the problem but it is not perfect. As this method is tedious and sometime, you might forget it.</p>
      <SwiftCodeBlock>
      <span className = "purple">var </span><span className ="blue">age:</span><span className = "purpleLight">Int</span>? = <span className = "purple">nil</span> <br/>
      <span className = "purple">if </span><span className ="blue">age</span> !&#61; <span className = "purple">nil</span>  &#123; <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;<span className ="purple">print</span>( <span className = "blue">age</span> * <span className = "darkblue">3</span>) <br/>
        &#125; <br/>
      </SwiftCodeBlock>

      <h3>2. Optional binding</h3>
      <p>A better way of doing it is to use a optional binding or if let which let you safety access
      the value inside the optional. In this method, there is no exclamation marks. If the value is 
      not nil then the first block of code will be executes which you can safety use the variable 
      inside the block of code.</p>
      <p>If it is it nil, then another block of code is excuted.</p>
      <SwiftCodeBlock>
      <span className = "purple">var </span><span className ="blue">age:</span><span className = "purpleLight">Int</span>? = <span className = "purple">nil</span> <br/>
      <span className = "purple">if </span><span className = "purple">let </span><span className ="blue">unwrappedAge</span> &#61; <span className = "purple">age</span>  &#123; <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;<span className ="purple">print</span>( <span className = "blue">unwrappedAge</span>) <br/>
        &#125; else &#123;<br/>
        &nbsp;&nbsp;&nbsp;&nbsp;<span className ="purple">print</span>( <span className = "red">"My age is not included"</span>) <br/>
        &#125;
      </SwiftCodeBlock>
      <br/>
      <SwiftCodeBlock output = {true}>
      My age is not included<br/>
      </SwiftCodeBlock>

      <p>However, because it is hard to name a variable, it’s common practice to give the unwrapped variable 
      the same name. </p>
      <SwiftCodeBlock>
      <span className = "purple">if </span><span className = "purple">let </span><span >age</span> &#61; <span className = "blue">age</span>  &#123; <br/>
        &nbsp;&nbsp;&nbsp;&nbsp;<span className ="purple">print</span>( <span className = "blue">age</span> ) <br/>
        &#125; else &#123;<br/>
        &nbsp;&nbsp;&nbsp;&nbsp;<span className ="purple">print</span>( <span className = "red">"My age is not included"</span>) <br/>
        &#125;
      </SwiftCodeBlock>

      <h3>3. Nil coalescing</h3>
      <p>Sometime you want to get a value out no matter what and in the case of nil, you will then 
      assign it want a default value. To do that, you use a double question mark (??). When it is nil, 
      it will assign it to the default value you stated of the right side of the double question mark.</p>
      <SwiftCodeBlock>
        <span className = "purple">var </span><span className ="blue">age:</span><span className = "purpleLight">Int</span>? = <span className = "purple">nil</span> <br/>
        <span className ="purple">print</span>( <span className = "blue">age ?? 0</span> * <span className = "darkblue">3</span>)
      </SwiftCodeBlock>
      <br/>
      <SwiftCodeBlock output = {true}>
      0<br/>
      </SwiftCodeBlock>

      <h3>4. Guard</h3>
      <p>Guard is useful to use if you want to execute a function if the condition is true. 
      The guard statement show below if the condition is false, the else block of code will be 
      excuted and then it return to end the function.</p>
      <SwiftCodeBlock>
      <span className = "purple">func </span><span className = "blue">getAge</span>(age: <span className = "purpleLight">Int</span>?)&#123;<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">guard </span><span className = "purple">let </span><span >age</span> &#61; <span className = "blue">age</span>  else &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">return </span> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&#125; <br/>
      &#125; 
      </SwiftCodeBlock>
      

      






      <SeeAlso>
        <Link><span className = "alink">Steppers' appearance</span></Link>
        <p className = "subtext">Changing the appearance of stepper using images and colors.</p>
        <Link><span className = "alink">UISliders</span></Link>
        <p className = "subtext">Similar to stepper. UISliders allow you to change the number without
        typing the numerous number.</p>
      </SeeAlso>
    </Layout>
    </Fragment>
  );
}

export default React.memo(App);
