import React, {Fragment} from 'react';
import classes from "./Properties.module.css";
import {Helmet} from "react-helmet";
import { HashLink as Link } from 'react-router-hash-link';

import Layout from '../../../../../Components/Layout/Layout';
import SwiftCodeBlock from '../../../../../Components/SwiftCodeBlock/SwiftCodeBlock';
import SeeAlso from '../../../../../Components/SeeAlso/SeeAlso';

function App() {
  return (
    <Fragment>
        <Helmet>
          <title>Properties - iOS Swift | TheCodeClass</title>
        </Helmet>

    <Layout mainTitle = "Swift References"
            longText = "What is Properties in Swift?"
            keyText = "Properties - iOS Swift"
            youtubesrc = ""
            downloadHref = ""
            referenceHref = "https://docs.swift.org/swift-book/LanguageGuide/Properties.html"
            styling = {classes.slidersContent}> 

      <p>There are two types of properties <strong>stored properties</strong> and <strong>computed properties</strong>.
      <strong>stored properties</strong> means they store actual values in the memory and <strong>computed properties</strong>,
      on the other hand, doesn't have actual memory allocated.
      </p>

      <h3>Stored properties</h3>
      <p>You should be familiar with stored properties already. Like that one shown below chefName and 
      age are stored properties.</p> 
      <p>The Chef structure has two properties and they are not assigned to a default value.</p>

      <SwiftCodeBlock>
      <span className = "purple">struct</span> <span className = "blue">Chef</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">let</span> <span className = "blue">chefName</span> : <span className = "purpleLight">String</span> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">var</span> <span className = "blue">age</span> : <span className = "purpleLight">Int</span> <br/>
      &#125; <br/>
      </SwiftCodeBlock>

      <p>Swift automatically creates an initializer for you based on the properties 
      you defined in your structure. Look at <a href = "/references/ios/swift/structure">structure reference </a>for more information.</p>

      <h3>Computed properties</h3>
      <p>Computed properties perform a calculation before storing the value.</p>

      <p>For example, salary is calculated based on the experienceLevel of the chef.</p>
      <SwiftCodeBlock>
      <span className = "purple">struct</span> <span className = "blue">Chef</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">let</span> <span className = "blue">chefName</span> : <span className = "purpleLight">String</span> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">var</span> <span className = "blue">experienceLevel</span> : <span className = "purpleLight">Int</span> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">var</span> <span className = "blue">salary</span> : <span className = "purpleLight">Int</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">return</span> (<span className = "blue">experienceLevel</span> * <span className = "darkblue">200</span>) + <span className = "darkBlue">3000</span><br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&#125;<br/>
      &#125; <br/>
      </SwiftCodeBlock>

      <h4>Getter and setter</h4>
      <p>Property that wrote above is read-only also know as getter. However, it is able to do 
      <strong>read-write computed property</strong> by writing a getter and a setter.</p>
      <p>By creating a setter, you are able to modify the new added value and made changes to the 
      other properties. For example, by setting the salary of the chef, you will update 
      the experienceLevel automatically.</p>

      <p><strong>newValue</strong> allow to access the new value assigned.</p>
      <SwiftCodeBlock>
      <span className = "purple">struct</span> <span className = "blue">Chef</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">let</span> <span className = "blue">chefName</span> : <span className = "purpleLight">String</span> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">var</span> <span className = "blue">experienceLevel</span> : <span className = "purpleLight">Int</span> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">var</span> <span className = "blue">salary</span> : <span className = "purpleLight">Int</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">get</span>&#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">return</span> (<span className = "blue">experienceLevel</span> * <span className = "darkblue">200</span>) + <span className = "darkblue">3000</span><br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">set</span>&#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "blue">experienceLevel</span> = newValue / <span className = "darkblue">200</span><br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&#125;<br/>
      &#125; <br/>
      </SwiftCodeBlock>

      <p>If you do not want to use new value, you can give it a name.</p>
      <SwiftCodeBlock>
      <span className = "purple">var</span> <span className = "blue">salary</span> : <span className = "purpleLight">Int</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">get</span>&#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">return</span> (<span className = "blue">experienceLevel</span> * <span className = "darkblue">200</span>) + <span className = "darkblue">3000</span><br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&#125;<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">set</span>(newSalary)&#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "blue">experienceLevel</span> = newSalary / <span className = "darkblue">200</span><br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&#125;<br/>
      &#125;<br/>
      </SwiftCodeBlock>

      <h4>Property observers</h4>
      <p>Are properties that are called before and after property changes.</p>
      <ul>
        <li><strong>willSet</strong> : Called just before the value is stored.</li>
        <li><strong>didSet</strong> : Called just after the value is changed.</li>
      </ul>
      <p>This cannot be use together with getters and setters.</p>
      <SwiftCodeBlock>
      <span className = "purple">var</span> <span className = "blue">salary</span> : <span className = "purpleLight">Int</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">willSet</span>&#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">print</span> (<span className = "red">"New value</span> \(newValue)<span className = "red">"</span>)<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&#125;<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">didSet</span>&#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">print</span> (<span className = "red">"New value</span> \(oldValue)<span className = "red">"</span>)<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&#125;<br/>
      &#125;<br/>
      </SwiftCodeBlock>

      <h4>Limiting a value</h4>
      <p>You can also use property observer to limit the value of the value if it is too high or low.</p>
      <SwiftCodeBlock>
      <span className = "purple">var</span> <span className = "blue">salary</span> : <span className = "purpleLight">Int</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">didSet</span>&#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">if </span>(<span className = "blue">salary</span> &gt; <span className = "darkblue">5000</span>)&#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">print</span> (<span className = "red">"Salary is too high. Salary reset to 3000"</span>)<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "blue">salary</span> = <span className = "darkblue">3000</span> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&#125;<br/>
      &#125;<br/>
      </SwiftCodeBlock>

      <h3>Lazy properties</h3>
      <p>Some properties might take a while to calculate and you do not want to slow down you app 
      as it need time to get the data from the net. By creating a lazy properties, the property will 
      only be calculated if you call the property.</p>

      <p>However, Lazy properties must have an initializer</p>

      <SwiftCodeBlock>
      <span className = "purple">struct</span> <span className = "blue">Chef</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">lazy</span> <span className = "purple">var</span> <span className = "blue">salary</span> = &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purple">return</span> (<span className = "darkBlue">3000</span> * <span className = "darkBlue">2</span>)<br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&#125;<br/>
      &#125; <br/>
      </SwiftCodeBlock>
      
      
      <SeeAlso>
        <Link><span className = "alink">Steppers' appearance</span></Link>
        <p className = "subtext">Changing the appearance of stepper using images and colors.</p>
        <Link><span className = "alink">UISliders</span></Link>
        <p className = "subtext">Similar to stepper. UISliders allow you to change the number without
        typing the numerous number.</p>
      </SeeAlso>
    </Layout>
    </Fragment>
  );
}

export default React.memo(App);
