import React, {Fragment} from 'react';
import classes from "./Switch.module.css";
import {Helmet} from "react-helmet";
import { HashLink as Link } from 'react-router-hash-link';

import Layout from '../../../../../Components/Layout/Layout';
import SwiftCodeBlock from '../../../../../Components/SwiftCodeBlock/SwiftCodeBlock';
import SeeAlso from '../../../../../Components/SeeAlso/SeeAlso';

function App() {
  return (
    <Fragment>
        <Helmet>
          <title>Switch - iOS Swift | TheCodeClass</title>
        </Helmet>

    <Layout mainTitle = "iOS References"
            longText = "How to create an Button in iOS"
            keyText = "Button - iOS Swift"
            youtubesrc = ""
            downloadHref = "https://github.com/evannwes/reference-ios-uikit-button/archive/master.zip"
            referenceHref = "https://developer.apple.com/documentation/uikit/uiswitch"
            styling = {classes.slidersContent}> 
      <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Button/UIButton.png?raw=true"
           alt = "UIButton"
           style = {{width: "300px"}}/>
      <p>In many apps, user have a choice to turn on or off features. Whenever a user have a 
      choice between two options, it is a good opportunity to use switch. When a switch is turned on,
      an color appeared (Green in the image above) and the switch appears on the right. When a switch is 
      turned off, the switch displays a different color and the switch appears on the left.</p>

      <p>You can also change the appearance easily by changing the color or adding an image to it.</p>

      <h3>Creating an UISwitch and Changing it's appearance</h3>

      <p id = "attributes"><b className = "step">Step 1: </b> In Main.storyboard file, click on the + icon to open the Object 
            Library window and drag and drop a Switch on the view.</p>
            <img src = "https://github.com/Sweekwang/TheCodeClass---img-storage/blob/master/iOS%20Reference/Components/Button/Button%20in%20libaray.png?raw=true"
                 alt = "UISwitch in libaray"
                 className = "landscapeImage"/>


    <p><b className = "step">Step 2:</b> Click on the Attributes Inspector icon or go to Choose View ➤ Inspectors ➤ Show Attributes Inspector.
            You will be able to see <b>attributes available for a progress view.</b> Change accordingly to the properties you want.</p>
    <p>Three common properties of a switch to modify</p>

      <SwiftCodeBlock>
        <span className = "purple">@IBOutlet weak var </span><span className ="blue">buttonName:</span> <span className = "purpleLight">UIButton!</span>
      </SwiftCodeBlock>

      <p id = "IBAction">Changing the text in the button programmatically.</p>
      <SwiftCodeBlock>
        <span className = "blue">spinnerName</span>.<span className ="purpleLight">setTitle</span>(<span className = "red">"New Title"</span>, for: <span className = "purpleLight">.normal</span>)
      </SwiftCodeBlock>

      <p><b className = "step">Step 3:</b> IBAction: Detecting a user click on the button.(Click on the UIButton, hold down the Control key, 
      and drag from the UIButton to the ViewController.swift file release and a popup window appears.)</p>
      <p>Choose the following parameters: connection equals <b>Action</b>, type equals <b> UIButton</b>, 
      Events equals <b> <a href = "s">Touch Up Inside</a></b> and give a name to it. <br/>
      </p>
      <SwiftCodeBlock>
      <span className = "purple">@IBAction func </span><span className ="blue">sliderSlide(</span> <span className = "purpleLight">_</span> sender: <span className = "purple">UISlider</span>)&#123;<br/>
      <span className ="purpleLight tab">print</span>( <span className = "red">"Clicked"</span> )
      <br/> &#125;
      </SwiftCodeBlock>



      <SeeAlso>
        <Link><span className = "alink">Steppers' appearance</span></Link>
        <p className = "subtext">Changing the appearance of stepper using images and colors.</p>
        <Link><span className = "alink">UISliders</span></Link>
        <p className = "subtext">Similar to stepper. UISliders allow you to change the number without
        typing the numerous number.</p>
      </SeeAlso>
    </Layout>
    </Fragment>
  );
}

export default React.memo(App);
