import React, {Fragment} from 'react';
import classes from "./Structure.module.css";
import {Helmet} from "react-helmet";
import { HashLink as Link } from 'react-router-hash-link';

import Layout from '../../../../../Components/Layout/Layout';
import SwiftCodeBlock from '../../../../../Components/SwiftCodeBlock/SwiftCodeBlock';
import SeeAlso from '../../../../../Components/SeeAlso/SeeAlso';

function App() {
  return (
    <Fragment>
        <Helmet>
          <title>Closure - iOS Swift | TheCodeClass</title>
        </Helmet>

    <Layout mainTitle = "Swift References"
            longText = "What is Structure in Swift?"
            keyText = "Structure - iOS Swift"
            youtubesrc = ""
            downloadHref = ""
            referenceHref = "https://docs.swift.org/swift-book/LanguageGuide/ClassesAndStructures.html"
            styling = {classes.slidersContent}> 

      <p>Structure is named type that can store properties (aka variable) and behaviour (aka function).
      You can declare a new type and use it in your code.</p>

      <h4>Why you need to create structure?</h4>
      <p>For example, you have a restaurant and this small business has only 1 chef. You write a program 
      to that contain the chef name and age and write a method for recipe they are going to cook. The 
      sudo code look like this</p>
      <ul>
        <li>chefName = "Wes"</li>
        <li>age = 26</li>
        <li>func cookSpaghetti (chefName ) &#123; <br/>
        &nbsp;&nbsp;&nbsp;&nbsp; chefName is cooking spaghetti
        &#125;
        </li>
      </ul>

      <p>Simple right? However, your businesses are very good and you have a lot of customers and you 
      need to hire 5 more chef. Then, then you have to write call each method for different chefs and 
      write mutiple variable to store their name. It will be hard to maintain.</p>

      <p><b className = "step">Step 1: </b> Basic Structure syntax: Creating a Structure. properties.</p>
      <p>You can create a structure that their name and age. chefName and age are called properties.
      <strong> Properties</strong> are constants or variables that are declared inside a structure or class.</p>

      <SwiftCodeBlock>
      <span className = "purple">struct</span> <span className = "blue">Chef</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">let</span> <span className = "blue">chefName</span> : <span className = "purpleLight">String</span> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">var</span> <span className = "blue">age</span> : <span className = "purpleLight">Int</span> <br/>
      &#125; <br/>
      </SwiftCodeBlock>
      <br/>

      <p><b className = "step">Step 1.1: </b> Initializer: instantiate a structure and store it in a constant or variable just like 
      any other data type.</p>
      <p>To create a chef, you can use the name of the type (Chef) along with a parameter list in parentheses by 
      creating a struct all the properties are enforce and you must have them which is a safety features.</p>
      <p><strong>Advantage of Swift Struct:</strong> you don’t need to declare this initializer in the Chef type as Swift automatically provides 
      initializers for structures with all the properties in the parameter list. </p>
      <SwiftCodeBlock>
      <span className = "purple">var</span> <span className = "blue">chefOne</span> = <span className = "blue">Chef</span>(chefName: <span className = "red">"Wes"</span>, age: <span className = "darkBlue">26</span>)
      </SwiftCodeBlock>

      <p><b className = "step">Step 1.2: </b> Accessing the properties.</p>
      <p>Now, you want to call your chef. To access it, you can use the dot syntax.</p>
      <SwiftCodeBlock>
      <span className = "purpleLight">print</span>( <span className = "blue">chefOne</span>.<span className = "blue">chefName</span> )
      </SwiftCodeBlock>

      <p><b className = "step">Step 1.2: </b> Changing the property' value.</p>
      <p>Today is your chef's birthday, and he is one year older. You can change the value by using assignment
      operator ( = ) because this it is a variable and not a constant.</p>
      <SwiftCodeBlock>
      <span className = "blue">chefOne</span>.<span className = "blue">age</span>  += <span className = "darkBlue">1</span>
      </SwiftCodeBlock>

      <p><b className = "step">Step 2: </b> Method. Structure can have capabilities to perform something.</p>

      <p>To create a method. You just have to create a function inside the structure. It is just a function 
      but it called method in class and structure.</p>
      <p>For example, chef, chef is able to cook stuff that are required for you store if someone ordered 
      them.</p>
      <SwiftCodeBlock>
      <span className = "purple">struct</span> <span className = "blue">Chef</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">let</span> <span className = "blue">chefName</span> : <span className = "purpleLight">String</span> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">var</span> <span className = "blue">age</span> : <span className = "purpleLight">Int</span> <br/> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">func</span> <span className = "blue">cookSpaghetti</span>()&#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "purpleLight">print</span>(<span className = "red">"</span>\(<span className = "blue">chefName</span>)<span className = "red"> is cooking spaghetti"</span>)<br/>
      &nbsp;&nbsp;&nbsp;&nbsp; &#125;<br/>
      &#125; <br/>
      </SwiftCodeBlock>
      <p>You can call the function by using dot notation like how you do for properties.</p>

      <p><b className = "step"> Mutating Method.</b></p>
      <p>Methods in the structures cannot change the value of the properties without marking as mutating. <br/>
      The <strong>mutating keyword</strong> marks a method that changes the value of the properties.</p>

      <SwiftCodeBlock>
      <span className = "purple">struct</span> <span className = "blue">Chef</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">let</span> <span className = "blue">chefName</span> : <span className = "purpleLight">String</span> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">var</span> <span className = "blue">age</span> : <span className = "purpleLight">Int</span> <br/> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">mutating func</span> <span className = "blue">updateAge</span>()&#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "blue">age</span> += 1<br/>
      &nbsp;&nbsp;&nbsp;&nbsp; &#125;<br/>
      &#125; <br/>
      </SwiftCodeBlock>

      <p><b className = "step"> Static properties or method</b> aka type methods or properties which allows 
      to access the data by calling the type.</p>
      <SwiftCodeBlock>
      <span className = "purple">struct</span> <span className = "blue">Chef</span> &#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">static var</span> <span className = "blue">age</span> = <span className = "blue">26</span> <br/> <br/>
      &nbsp;&nbsp;&nbsp;&nbsp; <span className = "purple">static func</span> <span className = "blue">updateAge</span>()&#123; <br/>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className = "blue">age</span> += 1<br/>
      &nbsp;&nbsp;&nbsp;&nbsp; &#125;<br/>
      &#125; <br/>
      </SwiftCodeBlock>
      <p>By adding a static, you can get the value without creating an object.</p>
      <SwiftCodeBlock>
      <span className = "purple">print</span>(Chef.<span className = "blue">age</span>)<br/>
      Chef.<span className = "blue">updateAge</span>() <br/>
      </SwiftCodeBlock>

      
      

      
      <SeeAlso>
        <Link><span className = "alink">Steppers' appearance</span></Link>
        <p className = "subtext">Changing the appearance of stepper using images and colors.</p>
        <Link><span className = "alink">UISliders</span></Link>
        <p className = "subtext">Similar to stepper. UISliders allow you to change the number without
        typing the numerous number.</p>
      </SeeAlso>
    </Layout>
    </Fragment>
  );
}

export default React.memo(App);
